  .btn-mobil-menu .path-rotation {
    height: 34px;
    width: 34px;
    transform-origin: 100% 0;
    margin: 34px 34px 0 0;
    transform: rotate(0deg);
  }

  .btn-mobil-menu .path-rotation:before {
    content: "";
    width: 30px;
    height: 34px;
    background-color: #f1f1f1;
    margin: 0 4px 0 0;
    transition: background-color .25s cubic-bezier(.4, 0, .2, 1);
    display: block;
  }

  @keyframes rotate-out {
    0% {
      transform: rotate(0deg);
    }

    40% {
      transform: rotate(180deg);
    }

    100% {
      transform: rotate(360deg);
    }
  }

  @keyframes rotate-in {
    0% {
      transform: rotate(360deg);
    }

    40% {
      transform: rotate(180deg);
    }

    100% {
      transform: rotate(0deg);
    }
  }

  .btn-mobil-menu .hamburglar.is-open .path {
