      stroke-dashoffset: 240px;
    }

    40% {
      stroke-dashoffset: 240px;
    }

    100% {
      stroke-dashoffset: 0;
    }
  }

  @keyframes dash-out {
    0% {
      stroke-dashoffset: 0;
    }

    40% {
      stroke-dashoffset: 240px;
    }

    100% {
      stroke-dashoffset: 240px;
    }
  }

  .btn-mobil-menu .burger-icon {
