    width: 68px;
    padding: 20px 16px;
    position: absolute;
  }

  .btn-mobil-menu .burger-container {
    height: 28px;
    width: 36px;
    position: relative;
  }

  .btn-mobil-menu .burger-bun-top, .btn-mobil-menu .burger-bun-bot, .btn-mobil-menu .burger-filling {
    height: 4px;
    width: 36px;
    background-color: #f1f1f1;
    border-radius: 2px;
    transition: background-color .25s cubic-bezier(.4, 0, .2, 1);
    display: block;
    position: absolute;
  }

  .btn-mobil-menu .burger-bun-top {
    transform-origin: 34px 2px;
    top: 0;
  }

  .btn-mobil-menu .burger-bun-bot {
    transform-origin: 34px 2px;
    bottom: 0;
  }

  .btn-mobil-menu .burger-filling {
    top: 12px;
  }

  .btn-mobil-menu .burger-ring {
    width: 68px;
    height: 68px;
    position: absolute;
    top: 0;
    left: 0;
  }

  .btn-mobil-menu .svg-ring {
    width: 68px;
    height: 68px;
  }

  .btn-mobil-menu .hamburglar.is-open .burger-bun-top {
    animation: bun-top-out .6s linear forwards;
  }

  .btn-mobil-menu .hamburglar.is-open .burger-bun-bot {
    animation: bun-bot-out .6s linear forwards;
  }

  .btn-mobil-menu .hamburglar.is-closed .burger-bun-top {
    animation: bun-top-in .6s linear forwards;
  }

  .btn-mobil-menu .hamburglar.is-closed .burger-bun-bot {
    animation: bun-bot-in .6s linear forwards;
  }

  @keyframes bun-top-out {
    0% {
      top: 0;
      left: 0;
      transform: rotate(0deg);
    }

    20% {
      top: 0;
      left: 0;
      transform: rotate(15deg);
    }

    80% {
      top: 0;
      left: -5px;
      transform: rotate(-60deg);
    }

    100% {
      top: 1px;
      left: -5px;
      transform: rotate(-45deg);
    }
  }

  @keyframes bun-bot-out {
    0% {
      left: 0;
      transform: rotate(0deg);
    }

    20% {
      left: 0;
      transform: rotate(-15deg);
    }

    80% {
      left: -5px;
      transform: rotate(60deg);
    }

    100% {
      left: -5px;
      transform: rotate(45deg);
    }
  }

  @keyframes bun-top-in {
    0% {
      bot: 0;
      left: -5px;
      transform: rotate(-45deg);
    }

    20% {
      bot: 0;
      left: -5px;
      transform: rotate(-60deg);
    }

    80% {
      bot: 0;
      left: 0;
      transform: rotate(15deg);
    }

    100% {
      bot: 1px;
      left: 0;
      transform: rotate(0deg);
    }
  }

  @keyframes bun-bot-in {
    0% {
      left: -5px;
      transform: rotate(45deg);
    }

    20% {
      bot: 0;
      left: -5px;
      transform: rotate(60deg);
    }

    80% {
      bot: 0;
      left: 0;
      transform: rotate(-15deg);
    }

    100% {
      left: 0;
      transform: rotate(0deg);
    }
