  .btn-mobil-menu .hamburglar.is-closed .burger-filling {
    animation: burger-fill-in .6s linear forwards;
  }

  @keyframes burger-fill-in {
    0% {
      width: 0;
      left: 36px;
    }

    40% {
      width: 0;
      left: 40px;
    }

    80% {
      width: 36px;
      left: -6px;
    }

    100% {
      width: 36px;
      left: 0;
    }
  }

  @keyframes burger-fill-out {
    0% {
      width: 36px;
      left: 0;
    }

    20% {
      width: 42px;
      left: -6px;
    }

    40% {
      width: 0;
      left: 40px;
    }

    100% {
      width: 0;
      left: 36px;
    }
  }

  .btn-mobil-menu .svg-ring {
    stroke: #f1f1f1;
    transition: stroke .25s cubic-bezier(.4, 0, .2, 1);
  }

  .btn-mobil-menu:hover .burger-bun-top, .btn-mobil-menu:hover .burger-bun-bot, .btn-mobil-menu:hover .burger-filling, .btn-mobil-menu:focus .burger-bun-top, .btn-mobil-menu:focus .burger-bun-bot, .btn-mobil-menu:focus .burger-filling, .btn-mobil-menu:hover .path-rotation:before, .btn-mobil-menu:focus .path-rotation:before {
    background: #daa35f;
  }

  .btn-mobil-menu:hover .svg-ring, .btn-mobil-menu:focus .svg-ring {
    stroke: #daa35f;
  }

  .btn-mobil-menu:active .burger-bun-top, .btn-mobil-menu:active .burger-bun-bot, .btn-mobil-menu:active .burger-filling, .btn-mobil-menu:active .path-rotation:before {
    background: #cb8d40;
  }

  .btn-mobil-menu:active .svg-ring {
    stroke: #cb8d40;
  }
}

