.footer {
    padding: 60px 0;
    background: #000000;

    .container {
        display: flex;
    }
}

.footer__nav-cont {
    display: flex;
    align-items: baseline;
    margin-right: 244px;
}

.footer__logo {
    margin-right: 186px;
}

.footer-nav-list__item+.footer-nav-list__item {
    margin-top: 20px;
}

.footer-nav-list__link {
    font-family: $secondary-font;
}


.social-list__item {
    font-size: 0;

    &+& {
        margin-top: 5px;
    }
}

.social-list__link {

    .social-list__icon {
        fill: currentColor;
        transition: fill 250ms cubic-bezier(0.4, 0, 0.2, 1);
    }

    &:hover,
    &:focus {
        .social-list__icon {
            fill: $hov-color;
        }
    }

    &:active {
        .social-list__icon {
            fill: $act-color;
        }
    }
}



.contacts-cont {
    margin-left: auto;
}

.contacts-title {
    font-family: $secondary-font;
    font-weight: 400;
    font-size: 20px;
    line-height: calc(29px/20px);
    letter-spacing: .02em;
    text-align: center;
    margin-bottom: 20px;
}

.contacts-list__item {

    &+& {
        margin-top: 20px;
    }

    .contacts-icon {
        margin-right: 8px;
    }
}